<template>
  <div class="book">
    <div v-if="book">
      <van-nav-bar
        :title="book.title"
        left-text="返回"
        :right-text="showTranslation ? '中' : 'En'"
        left-arrow
        fixed
        @click-right="showTranslation = !showTranslation"
      />
      <img class="book-image" :src="book.dir + '/book.jpg'" />
      <van-cell-group>
        <van-cell
          v-for="article in book.articles"
          :key="article.order"
          :title="showTranslation ? article.title : article.titleTranslation"
          :label="article.order"
          is-link
          center
          :to="{ name: 'repeater', params: { book, article } }"
        />
      </van-cell-group>
    </div>
    <van-skeleton v-else title :row="5" />
  </div>
</template>

<script>
import books from '@/lib/books.js'
import Vue from 'vue'
import { Cell, CellGroup, NavBar, Skeleton } from 'vant'

Vue.use(Cell)
  .use(CellGroup)
  .use(NavBar)
  .use(Skeleton)

export default {
  data() {
    return {
      book: null,
      showTranslation: false
    }
  },

  async created() {
    let book = { title: '新概念英音第三册', sn: '037F' }
    this.book = await books.getBook(book)
  }
}
</script>

<style lang="less" scoped>
.book {
  .book-image {
    width: 100%;
  }
}
</style>
