/*
localStorage['vocabulary']:
{
  bookId : 
  {
    articleId: [ {word: '', date: 0} ]
  }
}
*/

function load() {
  return JSON.parse(localStorage.getItem('vocabulary'))
}

function save(vocabulary) {
  localStorage.setItem('vocabulary', JSON.stringify(vocabulary))
}

export default {
  namespaced: true,
  state: {
    vocabulary: null
  },

  getters: {
    book: state => bookId => {
      if (bookId === '') return null
      // 如果没有数据则从缓存加载
      // if (!state.vocabulary)
      //   state.vocabulary =  load() || {}

      return (state.vocabulary[bookId] = state.vocabulary[bookId] || {})
    },

    article: (state, getters) => (bookId, articleId) => {
      let book = getters.book(bookId)
      if (!book) return null

      return (book[articleId] = book[articleId] || [])
    },

    have: (state, getters) => (word, bookId, articleId) => {
      let article = getters.article(bookId, articleId)
      if (!article) return false

      return article.some(item => item.word === word)
    }
  },

  mutations: {
    load(state) {
      state.vocabulary = load() || {}
    },

    add(state, { word, bookId, articleId }) {
      let have = this.getters['vocabulary/have']
      let getArticle = this.getters['vocabulary/article']

      if (word === '' || bookId === '' || articleId === '') return
      if (have(word, bookId, articleId)) return

      let article = getArticle(bookId, articleId)
      article.push({ word, date: Date.now() })

      save(state.vocabulary)
    },

    remove(state, { word, bookId, articleId }) {
      let getArticle = this.getters['vocabulary/article']
      let article = getArticle(bookId, articleId)
      let index = article.findIndex(item => item.word === word)
      if (index === -1) return

      article.splice(index, 1)
      save(state.vocabulary)
    }
  },

  actions: {}
}
