export default {
  namespaced: true,
  state: {
    current: -1, // 当前播放的句子索引
    count: -1 // 句子总数
  },
  mutations: {
    init(state, value) {
      state.count = value
      state.current = 0
    },

    // 设置current
    current(state, value) {
      state.current = value
    },

    // 转到下一个Section
    next(state) {
      let index = state.current + 1
      if (index > state.count) index = 0
      this.commit('sections/current', index)
    },

    // 转到上一个Section
    last(state) {
      let index = state.current - 1
      if (index < 0) index = 0
      this.commit('sections/current', index)
    }
  },
  actions: {}
}
