<template>
  <span class="sentence" v-html="html" @click="onClick"></span>
</template>

<script>
import npl from '@/lib/npl.js'

export default {
  props: {
    value: String,
    marks: {
      type: Array,
      default: () => []
    }
  },

  computed: {
    html() {
      if (!this.value) return ''

      // 提取单词并加上标签
      let html = npl.replace(this.value, word => {
        let isMark = this.marks && this.marks.indexOf(word) !== -1
        return `<word class="${isMark ? ' mark' : ''}">${word}</word>`
      })

      return html
    }
  },

  methods: {
    onClick(ev) {
      let dom = ev.target
      if (dom.tagName.toLowerCase() === 'word') {
        let text = dom.innerText
        this.$emit('word-click', { text, dom })
        ev.stopPropagation()
      }
    }
  }
}
</script>

<style></style>
