<template>
  <div class="article" @scroll="onScroll">
    <h1>{{ value.title }}</h1>

    <div class="raw-text" v-if="showRawText">
      <p v-for="(paragraph, index) in paragraphs" :key="index">
        <KuiSentence
          v-for="(item, index) in paragraph"
          :key="index"
          :class="{ current: item.index === current }"
          :value="item.text"
          :marks="marks"
          @dblclick.native="onSentenceClick(item.index)"
          v-longpress="
            () => {
              onSentenceClick(item.index)
            }
          "
          @word-click="onWordClick"
        ></KuiSentence>
      </p>
    </div>

    <div class="translation" v-if="showTranslation">
      <p v-for="(paragraph, index) in paragraphs" :key="index">
        <span
          v-for="(item, index) in paragraph"
          :key="index"
          :class="{ current: item.index === current }"
          @click="onSentenceClick(item.index)"
          >{{ item.translation }}</span
        >
      </p>
    </div>

    <van-icon
      class="anchor"
      name="location"
      size="26"
      v-show="currentOutOfViewport"
      @click="anchorCurrentSentence"
    />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import KuiSentence from '@/components/Sentence.vue'

export default {
  name: 'kui-article',

  props: {
    value: Object,
    marks: Array,
    showRawText: {
      type: Boolean,
      default: true
    },
    showTranslation: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      currentOutOfViewport: false
    }
  },

  computed: {
    // 划分段落
    paragraphs() {
      if (!this.value || !this.value.sections) return null

      return this.value.sections.reduce(
        (p, item, index) => {
          let last = p[p.length - 1]
          item.index = index
          last.push(item)

          // 产生新段落
          if (item.text && item.text.endsWith('\n')) p.push([])

          return p
        },
        [[]]
      )
    },
    ...mapState('sections', ['current'])
  },

  watch: {
    current() {
      this.onScroll()
    }
  },

  components: {
    KuiSentence
  },

  methods: {
    onSentenceClick(index) {
      this.$store.commit('sections/current', index)
    },

    onWordClick({ text, dom }) {
      this.$emit('word-click', { text, dom })
    },

    anchorCurrentSentence() {
      this.$el.querySelector('.current').scrollIntoView({
        behavior: 'smooth', // 平滑过渡
        block: 'start' // 上边框与视窗顶部平齐。默认值
      })
    },

    onScroll() {
      const el = this.$el.querySelector('.current')
      if (!el) return
      this.currentOutOfViewport = !this.isElementInViewport(el)
    },

    isElementInViewport(el, offset = 0) {
      const box = el.getBoundingClientRect()
      let top = box.top >= 0
      let left = box.left >= 0
      let bottom =
        box.bottom <=
        (window.innerHeight || document.documentElement.clientHeight) + offset
      let right =
        box.right <=
        (window.innerWidth || document.documentElement.clientWidth) + offset
      return top && left && bottom && right
    }
  }
}
</script>

<style lang="less">
.article {
  height: 100%;
  overflow: scroll;

  h1 {
    font-size: 0.26rem;
    text-align: center;
  }

  p {
    line-height: 1.8;
    margin-block-start: 0.8em;
    margin-block-end: 0.8em;
  }

  .sentence {
    user-select: none;

    .mark {
      color: #666699;
      font-weight: bold;
    }
  }

  .raw-text,
  .translation {
    white-space: pre-wrap;
  }

  .current {
    color: #cc6600;
  }

  .anchor {
    position: fixed;
    bottom: 10px;
    left: 10px;
    z-index: 100;
  }
}
</style>
