<template>
  <van-cell
    class="dict"
    :label-class="'label ' + (collapse && 'label-collapse')"
    @click="collapse = !collapse"
    v-if="result"
    :label="explain"
    :border="border"
    center
  >
    <div slot="title">
      <span class="word">{{ result.query }}</span>
      <span class="phonetic" v-if="result.phonetic">
        [{{ result.phonetic }}]</span
      >
    </div>
    <van-icon
      slot="right-icon"
      :name="star ? 'star' : 'star-o'"
      @click.stop="onStar"
    />
  </van-cell>
</template>

<script>
import Vue from 'vue'
import { Cell, Icon } from 'vant'
import dict from '@/lib/dict.js'

Vue.use(Cell).use(Icon)

export default {
  props: {
    word: String,
    bookId: String,
    articleId: String,
    border: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      result: null,
      collapse: true
    }
  },

  computed: {
    star() {
      return this.$store.getters['vocabulary/have'](
        this.word,
        this.bookId,
        this.articleId
      )
    },

    explain() {
      if (!this.result || !this.result.explains) return null

      let res = this.result.explains
      // if (!this.expand && res.lenght > 3) {
      //   res = res.slice(0, 3)
      //   res.push('点击查看全部')
      // }

      return res.join('\n')
    }
  },

  methods: {
    async lookup() {
      this.result = await dict.lookup(this.word)
    },

    onStar() {
      // 添加删除生词
      let { word, bookId, articleId } = this

      if (this.star)
        this.$store.commit('vocabulary/remove', { word, bookId, articleId })
      else this.$store.commit('vocabulary/add', { word, bookId, articleId })
    }
  },

  created() {
    this.lookup()
  },

  watch: {
    word() {
      this.lookup()
    }
  }
}
</script>

<style lang="less" scoped>
.word {
  font-weight: bolder;
}

.label {
  white-space: pre-wrap;
}

.label-collapse {
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3; //规定几行显示省略号
  -webkit-box-orient: vertical;
}
</style>
