<template>
  <div class="repeater">
    <van-nav-bar
      :title="article.titleTranslation"
      left-text="返回"
      left-arrow
      @click-left="$router.go(-1)"
    />
    <van-tabs
      v-model="tabIndex"
      class="main"
      animated
      @click.native="word = ''"
    >
      <van-tab class="tab-article" title="全文">
        <KuiArticle
          :value="article"
          :marks="words"
          @word-click="onWordClick"
        ></KuiArticle>
      </van-tab>
      <van-tab class="tab-sentence" title="句子">
        <div class="content" v-if="currentSection && currentSection.text">
          <KuiSentence
            :value="currentSection.text"
            @word-click="onWordClick"
          ></KuiSentence>
          <van-overlay
            class-name="overlay"
            :show="showSentence"
            @click="showSentence = false"
          />
          <van-icon
            class="showSentence"
            name="eye"
            size="26"
            :color="showSentence ? 'white' : 'unset'"
            @click="showSentence = !showSentence"
          />
        </div>
        <van-skeleton v-else :row="3" />
      </van-tab>
      <van-tab class="tab-vocabulary" title="生词">
        <KuiVocabulary :bookId="bookId" :articleId="articleId"></KuiVocabulary>
      </van-tab>
    </van-tabs>
    <KuiPlayer ref="domPlayer"></KuiPlayer>
    <KuiDict
      class="dict"
      :word="word"
      :bookId="bookId"
      :articleId="articleId"
      :border="false"
    ></KuiDict>
    <div
      class="current-translation"
      v-if="currentSection"
      v-show="tabIndex !== 2"
      @click="showTranslation = !showTranslation"
    >
      {{ showTranslation ? currentSection.translation : '点击显示翻译' }}
    </div>
    <div class="button-bar">
      <van-icon class="item" name="replay" @click="player.seekStart()" />
      <van-icon
        class="item"
        name="arrow-left"
        @click="$store.commit('sections/last')"
      />
      <van-icon class="item" :name="playState" @click="player.toggle()" />
      <van-icon
        class="item"
        name="arrow"
        @click="$store.commit('sections/next')"
      />
      <van-icon
        class="item"
        @click="player.mode = player.mode ? 0 : 1"
        :name="playMode"
      />
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { mapState } from 'vuex'
import Article from '@/lib/article.js'
import books from '@/lib/books.js'
import KuiSentence from '@/components/Sentence.vue'
import KuiArticle from '@/components/Article'
import KuiPlayer from '@/components/Player'
import KuiVocabulary from '@/components/Vocabulary'
import KuiDict from '@/components/Dict'
import { Tab, Tabs, Skeleton, NavBar, Overlay } from 'vant'

Vue.use(Tab)
  .use(Tabs)
  .use(Skeleton)
  .use(NavBar)
  .use(Overlay)

export default {
  components: {
    KuiSentence,
    KuiArticle,
    KuiPlayer,
    KuiVocabulary,
    KuiDict
  },

  data() {
    return {
      bookId: '',
      articleId: '',
      article: {},
      player: null, // lib/Player的实例
      word: '', // 当前查询的单词
      tabIndex: 0,
      showSentence: false,
      showTranslation: true
    }
  },

  computed: {
    // 文章中的生词
    words() {
      let { bookId, articleId } = this
      let store = this.$store.getters['vocabulary/article'](bookId, articleId)
      if (store) return store.map(item => item.word)
      return []
    },
    playState() {
      return this.player && this.player.isPlaying()
        ? 'pause-circle'
        : 'play-circle'
    },
    playMode() {
      return this.player && this.player.mode ? 'exchange' : 'play-circle-o'
    },
    currentSection() {
      if (this.article && this.article.sections && this.current !== -1)
        return this.article.sections[this.current]
      else return null
    },
    ...mapState('sections', ['current'])
  },

  async created() {
    // console.log('repeater', this.$route.params)
    let { book, article } = this.$route.params
    let lrc = await books.getArticleLrc(book, article)
    let media = books.getMediaPath(book, article)

    this.article = new Article(lrc)
    this.bookId = books.getBookId(book)
    this.articleId = books.getArticleId(this.article)
    this.$store.commit('sections/init', this.article.sections.length)

    let sections = this.article.sections.map(item => item.time)
    this.$refs.domPlayer.open(media, sections)
    this.player.play()
  },

  mounted() {
    this.player = this.$refs.domPlayer.player
  },

  beforeDestroy() {
    this.player.stop()
  },

  methods: {
    // 处理单词点击事件
    onWordClick({ text, dom }) {
      // 查词
      this.word = text

      // 标记单词
      this._dom && this._dom.classList.remove('select-word')
      dom.classList.add('select-word')
      this._dom = dom
    }
  }
}
</script>

