import { Howl } from 'howler'

class Player {
  //播放模式
  static MODE_NORMAL = 0 //普通模式
  static MODE_REPEAT = 1 //复读模式

  constructor() {
    //变量声明
    this.domAudio = null //音频对象
    this.mode = Player.MODE_NORMAL //播放模式
    // this.state = 0; // 播放状态
    // this.isSeeking = false;

    //------------复读小节----------
    this.sectionStart = 0 //播放开始小节
    this.sectionEnd = 0 //播放结束小节
    this.repeatTimes = 0 // 已复读次数

    //------------句间停顿--------------
    // this.gapTurn = false;//暂停开关
    // this.gapTime = 3;//间歇时间 单位秒
    // this._gapInterval = null;//暂停状态(计时器)

    // 事件
    this.onTimeUpdated = null
    this.onSectionOnce = null // 小节播放完1次
    // this.onStateChanged = null; //
  }

  //初始化音频文件，传参地址
  open(url) {
    this.domAudio = new Howl({
      src: url
    })
    // this.play()

    // 初始化变量
    this.sectionStart = 0 // 初始化音频起始点
    this.sectionEnd = this.duration() // 初始化音频结束点
  }

  // 播放
  play() {
    if (this.isPlaying()) return

    this.domAudio.play()

    this._playInterval = setInterval(() => {
      this._positionChanged()
    }, 300)

    // 清除句间停顿定时器
    // clearTimeout(Player._gapInterval)
  }

  // 暂停
  pause() {
    this.domAudio.pause()
    clearInterval(this._playInterval)
  }

  stop() {
    this.domAudio.stop()
    clearInterval(this._playInterval)
  }

  isPlaying() {
    if (!this.domAudio) return false

    return this.domAudio.playing()
  }

  // 切换播放状态
  toggle() {
    if (this.isPlaying()) this.pause()
    else this.play()
  }

  // 获取当前位置
  position() {
    if (!this.domAudio) return 0

    return parseInt(this.domAudio.seek() * 1000)
  }

  seek(pos) {
    if (this.domAudio) this.domAudio.seek(pos / 1000)
  }

  seekStart() {
    this.seek(this.sectionStart)
  }

  _positionChanged() {
    if (this._isOutOfSection()) {
      this._sectionOnce()

      if (this.mode === Player.MODE_REPEAT) this.seekStart()
    }

    this.onPositionChanged && this.onPositionChanged(this.position())
  }

  _sectionOnce() {
    this.repeatTimes++
    this.onSectionOnce && this.onSectionOnce()
  }

  // 设置复读小节
  section(start, end) {
    if (end < start) {
      throw '复读区间设置异常 end < start'
    }

    this.sectionStart = start
    this.sectionEnd = end
    this.repeatTimes = 0
  }

  _isOutOfSection() {
    let pos = this.position()
    return pos > this.sectionEnd || pos < this.sectionStart
  }

  //获取或设置静音开关
  muted(value) {
    if (arguments.length === 0) {
      return this.domAudio.muted
    } else {
      this.domAudio.muted = value
    }
  }

  //获取或设置音量
  volume(value) {
    if (arguments.length === 0) {
      return this.domAudio.volume
    } else {
      this.domAudio.volume = value
    }
  }

  //获取音频时长
  duration() {
    return this.domAudio.duration()
  }
}

export default Player
