<template>
  <div></div>
</template>

<script>
import { mapState } from 'vuex'
import Player from '@/lib/player.js'

export default {
  data() {
    return {
      player: new Player(),
      sections: []
    }
  },

  computed: mapState('sections', ['current']),

  watch: {
    current(index) {
      this.section(index)
    }
  },

  created() {
    this.player.onSectionOnce = () => {
      if (this.player.mode === Player.MODE_NORMAL)
        this.$store.commit('sections/next')
    }
  },

  methods: {
    section(index) {
      let current = this.sections[index]
      let next = this.sections[index + 1]
      this.player.section(current, next)
      this.player.seekStart()
    },

    open(url, sections) {
      this.player.open(url)
      this.sections = sections
    }
  }
}
</script>
