import axios from 'axios'

const baseUrl =
  'https://bird.ioliu.cn/v2?url=http://fanyi.youdao.com/openapi.do?keyfrom=repeator&key=436780866&type=data&doctype=json&only=dict&version=1.1&q='

class Dict {
  async lookupByWeb(word) {
    let { data } = await axios.get(baseUrl + word)

    // 缓存数据
    let cache = this.loadCache()
    let { query, basic } = data
    let result = { query, ...basic }

    cache[word] = result
    localStorage.setItem('dictCache', JSON.stringify(cache))

    return result
  }

  lookupByCache(word) {
    let cache = this.loadCache()
    return cache[word]
  }

  async lookup(word) {
    if (!word || word === '') return null
    let result = this.lookupByCache(word)
    if (result) return result

    return await this.lookupByWeb(word)
  }

  loadCache() {
    let cache = localStorage.getItem('dictCache')
    if (cache) return JSON.parse(cache)

    return {}
  }
}

export default new Dict()
