import axios from 'axios'

class Books {
  getBookId({ title, sn }) {
    return `${title}_${sn}`
  }

  getArticleId({ order, title }) {
    return `[${order}]${title}`
  }

  async getBook({ title, sn }) {
    let bookId = this.getBookId({ title, sn })
    let dir = `/books/${bookId}`
    let res = await axios.get(`${dir}/book.json`)
    let book = { ...res.data, dir }
    return book
  }

  _getArticleBasePath(book, article) {
    let bookId = this.getBookId(book)
    let articleId = this.getArticleId(article)
    return `/books/${bookId}/${articleId}`
  }

  async getArticleLrc(book, article) {
    let path = this._getArticleBasePath(book, article)
    let res = await axios.get(`${path}.lrc`)
    return res.data
  }

  getMediaPath(book, article) {
    let path = this._getArticleBasePath(book, article)
    return `${path}.mp3`
  }
}

export default new Books()
