// 获取歌词的方法    需要把歌词当做数据传递进去 ，会得到解析好的歌词数据
// 将LRC解析成Article对象
import Lrc from './lrc.js'

class Article {
  constructor(lrcText) {
    let lrc = new Lrc(lrcText)

    this.artist = lrc.tags.artist
    this.album = lrc.tags.album
    this.title = lrc.tags.title
    this.titleTranslation = lrc.tags.tt
    this.order = lrc.tags.order
    this.duration = lrc.lines[lrc.lines.length - 1].time
    this.sections = this.content(lrc.lines)
  }

  //获取Lrc中的英文翻译
  content(lines) {
    var arr = []

    if (lines[0].time != 0) {
      arr.push({ text: null, translation: null, time: 0 })
    }

    for (var i = 0; i < lines.length; i++) {
      var json = {}
      if (lines[i + 1] == undefined) {
        if (lines[i].txt.split('^').length == 2) {
          json.text = lines[i].txt.split('^')[0] //英文翻译
          json.translation = lines[i].txt.split('^')[1] //中文翻译
        } else {
          json.text = lines[i].txt.split('^')[0]
          json.translation = ' '
        }
      } else {
        if (lines[i + 1].originLineNum - lines[i].originLineNum > 1) {
          if (lines[i].txt.split('^').length == 2) {
            json.text = lines[i].txt.split('^')[0] + '\n' //英文翻译
            json.translation = lines[i].txt.split('^')[1] + '\n' //中文翻译
          } else {
            json.text = lines[i].txt.split('^')[0] + '\n'
            json.translation = ' ' + '\n'
          }
        } else {
          if (lines[i].txt.split('^').length == 2) {
            json.text = lines[i].txt.split('^')[0] //英文翻译
            json.translation = lines[i].txt.split('^')[1] //中文翻译
          } else {
            json.text = lines[i].txt.split('^')[0]
            json.translation = ' '
          }
        }
      }

      json.time = lines[i].time //添加时间
      arr.push(json)
    }
    return arr
  }
}

export default Article
