<template>
  <div class="vocabulary">
    <KuiDict
      v-for="(item, index) in article"
      :key="index"
      :word="item"
      :bookId="bookId"
      :articleId="articleId"
    ></KuiDict>
  </div>
</template>

<script>
import KuiDict from '@/components/Dict'

export default {
  components: {
    KuiDict
  },

  props: {
    bookId: String,
    articleId: String
  },

  data() {
    return {}
  },

  computed: {
    // 文章中的生词
    article() {
      let { bookId, articleId } = this
      let store = this.$store.getters['vocabulary/article'](bookId, articleId)
      return store.map(item => item.word)
    }
  },

  created() {}
}
</script>

